import * as actions from '../actions'

const ordersInitialState = {list:[], page: 1}
const orders = (state = ordersInitialState, action) => {
  switch (action.type) {
    case actions.SET_ORDERS:
      return {
        ...state
      }
    default:
      return state
  }
}

export default orders


