import React from 'react'
import { LinkContainer, IndexLinkContainer } from 'react-router-bootstrap'
import { Navbar, Nav, NavItem } from 'react-bootstrap'

const NavBar = ({user, logout}) => {
  var adminNav = <div>
    <Nav>
      <LinkContainer to={'/orders'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </LinkContainer>
      <LinkContainer to={'/product/create'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </LinkContainer>
      </Nav>
  </div>
    
  var userNav = <Nav>
      <LinkContainer to={'/orders'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </LinkContainer>
      <LinkContainer to={'/carts'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </LinkContainer>
      </Nav> 

  var accountNav = user.isLogin? <Nav pullRight>
      <NavItem onClick={logout}></NavItem>
      </Nav> : <Nav pullRight>
      <LinkContainer to={'/register'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </LinkContainer>
      <LinkContainer to={'/login'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </LinkContainer>
    </Nav>

  return(
    <Navbar>
    <Navbar.Header>
      <Navbar.Brand>
        <a href="/"></a>
      </Navbar.Brand>
    </Navbar.Header>
    <Nav>
      <IndexLinkContainer to={'/'}>
        <NavItem eventKey={1} href="#"></NavItem>
      </IndexLinkContainer>
    </Nav>
    {(user.isAdmin && user.isLogin) ? adminNav : '' }
    {(!user.isAdmin && user.isLogin) ? userNav : '' }
    {accountNav}
    
    </Navbar>
  )
}

export default NavBar


