//  GET:/orders/(page)
//   {list:[{products:[],id,  date, price}]}

//   
//   POST:/orders/create
//   : {status:success|error}

export default {
  getAll: (page, scb, fcb) => {
    fetch('/orders/' + page, {
      credentials: 'include',
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
      }).then((res) => {
        return res.json()
      }).then((json) => {
        if (json) {
          scb(json)
        }
      })
  }
}


