import React from 'react'
import { Row, Media } from 'react-bootstrap'

class Cart extends React.Component {
  render () {
    return (
      <Row>
        <Media>
          <Media.Left>
            <img width={64} height={64} src={this.props.product.src} role="presentation" />
          </Media.Left>
          <Media.Body>
            <Media.Heading>{this.props.product.name}</Media.Heading>
            <p>{this.props.product.intro}</p>
          </Media.Body>
        </Media>
      </Row>
    )
  }
}

export default Cart


