import React from 'react'
import { ListGroup, ListGroupItem } from 'react-bootstrap'

const Order = ({order}) => {
  const orderItems = order.list.map((product) => {
    <ListGroupItem>{product.name + '  -' + product.price}</ListGroupItem>
  })
  return(
    <ListGroup>
      {orderItems}
    </ListGroup>
  )
}

export default Order


