import React from 'react'
import Order from './Order'
import { Col } from 'react-bootstrap'

const Orders = ({orders}) => {
  const ordersComponent = orders.map((order) => {
    <Order order={order}></Order>
  })
  return(
    <Col xs={8} xsOffset={2}>
      {ordersComponent}
    </Col> 
  )
}

export default Orders



